			"description": "來「雙連市場」逛逛決定早餐吃什麼吧，滷肉飯、潤餅、蔥花捲等小吃應有盡有，吃完再買杯青草茶或來碗圓仔湯。另外市場內有保佑功名、升遷的「文昌宮」，據說相當靈驗！ "
